



import { Route, Redirect, Switch } from "react-router-dom";

import "./index.scss";

import Lazyload from "&/lazyload";

export default class Setting extends Component {

    render() {

        return (

            <div className="setting">
                <Route path="/index/setting/usermanage" component={Lazyload(() => import("../usermanege"))}></Route>
                <Route path="/index/setting/rolemanage" component={Lazyload(() => import("../rolemanage"))}></Route>
                <Route path="/index/setting/jurisdictionmanage" component={Lazyload(() => import("../jurisdictionmanage"))}></Route>
            </div>
        )
    }
}